import React from 'react'
import styled from 'styled-components'

import background from '../assets/homebg2.jpg'
import tsLogo from '../assets/ts-logo-sq.png'
import ngLogo from '../assets/ng-logo.png'

const BG = styled.div`
  background: url(${background}) no-repeat center;
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
`

const Full = styled.div`
  ${'' /* background: url(${background}) no-repeat center; */}
  box-sizing: border-box;
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 2em;
  padding-top: 3em;
`

const Logo = styled.img`
  max-width: 200px;
  width: 100%;
  transition: all 300ms;

  @media (max-width: 780px) {
    max-width: 200px;
    width: 100%;
  }

  @media (max-width: 480px) {
    max-width: 120px;
    width: 100%;
  }
`

const Company = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1em;

  @media (max-width: 780px) {
    flex-direction: column;
  }
`

const X = styled.span`
  color: white;
  font-size: 50px;
  padding: 0 1em 10px 1em;
`

const Detail = styled.div`
  text-align: center;
`

const Special = styled.h3`
  color: #fcb03a;
`

const Hero = () => (
  <div>
    <BG />
    <Full>
      <Company>
        <Logo src={tsLogo} />
        <X>&times;</X>
        <Logo src={ngLogo} />
      </Company>
      <Detail>
        <h1>JUNE 22 - 23, 2018</h1>
        <h2>CENTARA GRAND AT CENTRAL WORLD</h2>
        <Special>STUDENT PRICE BY NISIT GENERATION</Special>
      </Detail>
    </Full>
  </div>
)
export default Hero



