import React, { Component } from 'react'
import styled from 'styled-components'
import Button from './Button'
import Hamburger from './Hamburger'
// import OverlayMenu from '../containers/OverlayMenu'
import logo from '../assets/ng-logo.png'

const Wrapper = styled.div`
  margin: auto;
  position: fixed;
  width: 100%;
  transition: all 600ms;
  background: rgb(0, 0, 0, 0);
`

const Nav = styled.nav`
  max-width: 1200px;
  display: flex;
  box-sizing: border-box;
  top: 0;
  left: 0;
  padding: 0 1em;
  align-items: center;
  margin: auto;
  ${'' /* background-color: rgb(0,0,0,0); */}
`

const Menu = styled.ul`
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  text-align: right;

  @media (max-width: 780px) {
    display: none;
  }
`

const Item = styled.li`
  float: left;

  a {
    display: block;
    padding: 1em 1em;
    margin: 0 .25em;
    font-weight: 500;
    color: white;
    text-decoration: none;
    transition: all 200ms;
  }

  &:last-child {
    margin-right: .5em;
  }

  a:hover {
    border-radius: 20px;
    background: hsla(0, 0%, 100%, 0.1);
  }
`

const Brand = styled.img`
  padding: .5em 0;
  height: 60px;
  transition: all 300ms;

  @media (max-width: 780px) {
    height: 40px;
  }
`

const Right = styled.div`
  display: flex;
  margin-left: auto;
`

const Collapse = styled.div`
  display: flex;
  @media (max-width: 780px) {
    display: none;
  }
`

const OnSmall = styled.div`
  display: none;
  @media (max-width: 780px) {
    display: block;
  }
`

const darkened = {
  background: 'rgb(0,0,0,0.8)',
}

const menus = [
  { name: 'HOME', href: '/home' },
  { name: 'TIMELINE', href: '/news' },
  { name: 'SPEAKERS', href: '/contact' },
  { name: 'STAGES', href: '/about' },
  { name: 'ABOUT US', href: '/about' },
]

class Navbar extends Component {
  state = {
    extended: true,
  }

  componentDidMount () {
    window.addEventListener('scroll', () => {
      if (window.scrollY > 150) {
        if (this.state.extended) {
          this.setState({
            extended: false,
          })
        }
      } else if (!this.state.extended) {
        this.setState({
          extended: true,
        })
      }
    })
  }

  render () {
    return (
      <Wrapper style={this.state.extended ? {} : darkened}>
        <Nav>
          <a href="/"><Brand src={logo} /></a>
          <Right>
            <Collapse>
              <Menu>
                {menus.map(menu => (
                  <Item key={menu.name}>
                    <a href={menu.href}>{menu.name}</a>
                  </Item>
                ))}
              </Menu>
              <Button>GET TICKET</Button>
            </Collapse>
            {/* <OnSmall>
              <Hamburger />
            </OnSmall> */}
          </Right>
        </Nav>
      </Wrapper>
    )
  }
}

export default Navbar



